.app {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #fefae0;
  color: #522258;
  font-size: 24px;
  font-weight: bold;
  font-family: "Courier New", Courier, monospace;
}

.gameboard {
  width: 300px;
  height: 300px;
  background-color: black;
  display: flex;
  flex-wrap: wrap;
  border: 1px solid black;
  border-radius: 15px;
}

.square {
  width: 100px;
  height: 100px;
  background-color: white;
  box-sizing: border-box;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.circle {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  border: 15px solid #03346e;
  box-sizing: border-box;
}

.cross {
  height: 90px;
  width: 90px;
  position: relative;
  transform: rotate(45deg);
}

.cross::before,
.cross::after {
  position: absolute;
  content: "";
  background-color: #800000;
  border-radius: 7px;
}

.cross::before {
  left: 50%;
  width: 30%;
  height: 100%;
  margin-left: -15%;
}

.cross::after {
  top: 50%;
  width: 100%;
  height: 30%;
  margin-top: -15%;
}

button {
  font: inherit;
  padding: 0.5em 1em;
  border: none;
  border-radius: 10px;
  background-color: #6c48c5;
  color: white;
  text-transform: uppercase;
  cursor: pointer;
}

button:hover {
  opacity: 0.8;
}

.visible {
  display: block;
}

.hidden {
  display: none;
}
